<template>
  <div class="todo-header">
      <input type="text" :value="inputData" @keyup.enter = "add" placeholder="请输入你的任务名称，按回车键确认" />
  </div>
</template>

<script>
export default {
  name: "TodoListHeader",
  data() {
    return {
      inputData:''
    }
  },
  props:["addTodo"],
  methods: {
    add(e) {
      let val = e.target.value.trim();
        if (val && val != this.inputData) {
          this.inputData = val;
           let aa = {
            content: val,
            status: false,
            id: Date.now()
          }
          this.addTodo(aa);
        } else if (val && val == this.inputData) {
          alert("该数据 已添加!!");
        }
    }
  }
}
</script>

