<template>
  <ul class="todo-main" >
    <span v-if="contentList.length == 0">--------没有内容--------</span>
    <li v-for="item in contentList" 
        :key="item.id"
        :class="{'check-bgd': item.status}"
        @click="checkF(item.id)">
        <!-- li元素已经代理了点击事件，若checkbox被 label 包裹，当点击label时，checkbox将被连续点击2次（由li来的click，和checkbox本身click特性 各一次），从而引发“勾选失败（实际对其连续勾选了2次）” 。故不能使用label包裹。另外，根据“事件冒泡”特性，点击span子元素中内容也能被父元素的click监听到，<label for="checkbox的id名">在这里没有意义！-->
        <input type="checkbox" :checked="item.status"/>
        <span :class="{ 'throuline': item.status }">{{ item.content }}</span>
      
        <!-- <LMButtom changeS ref="flagB"></LMButtom> -->
          <button class="btn btn-danger" @click="delTodoF(item.id)">删除</button>
    </li>
</ul>
</template>

<script>

export default {
  name: "TodoListMain",
  props:["contentList","check","delTodo"],
  data() {
    return {
    }
  },
  methods: {
    checkF(id) {
      this.check(id);
    },
    delTodoF(id) {
      this.delTodo(id);
    }
  }
}
</script>

