function saveData(key, value) {
    if (value) {
        // 当value不为空时才执行本地存储，避免 因之前有key，值为空，无法加载 假数据
        localStorage.setItem(key, JSON.stringify(value));
    }
}
function getData(key) {
    try {
        const db = localStorage.getItem(key);
        if (!db) {
            // 假数据
            let contentList = [
                { id: 0, content: "xxx", status: true },
                { id: 1, content: "yyy", status: false },
                { id: 2, content: "thrthrt", status: true }
            ]
            localStorage.setItem("contentList", JSON.stringify(contentList));
            return JSON.parse(contentList);
        }
        else {
            return JSON.parse(db);
        }
    }
    catch (error) {
        // 加载本地数据出错
        console.error('Error while loading data from localStorage:', error);
    }
}

export { saveData, getData };
