<template>
  <div class="todo-footer">
    <label>
        <input type="checkbox" 
         @click="checkAllF"
         :checked="checkSum"
         ref="noCheck"
         :disabled="isDisabled"
         />
    </label>
    <span>已完成{{ doneSum }} / 全部{{ contentList.length }}</span>&nbsp;&nbsp;
    <span :style="{fontSize: '10px',color:'red'}">数据更改 1s 后 自动保存！！</span>
    <button class="btn btn-danger"
    @click="deleteAllF"
    >清除已完成任务</button>
</div>
</template>

<script>
export default {
  name: "TodoListFooter",
  props: ["doneSum", "checkSum", "contentList", "checkAll", "deleteAll"],
  computed: {
    isDisabled() {
      // 若contentList中没有数据，直接禁用全选框
     return this.contentList.length == 0 ? true : false;
    }
  },
  methods: {
    checkAllF() {
      this.checkAll( ! this.checkSum );
    },
    deleteAllF() {
      this.deleteAll();
    }
  }
}
</script>

