<template>
      <div id="root">
          <div class="todo-container">
              <div class="todo-wrap">
                <TodoListHeader :addTodo="addTodo" />
                <TodoListMain
                :contentList="contentList"
                :check="check"
                :delTodo="delTodo"
                />
                <TodoListFooter
                 :doneSum="doneSum" 
                 :checkSum="checkSum" 
                 :contentList="contentList"
                 :checkAll="checkAll"
                 :deleteAll="deleteAll"
                 />
                </div>
          </div>
      </div>
</template>

<script>
import TodoListHeader from '@/components/TodoListHeader.vue'
import TodoListMain from '@/components/TodoListMain.vue'
import TodoListFooter from '@/components/TodoListFooter.vue'
// 这种直接在.vue文件中引入js的方式，可直接在当前.vue文件中使用“函数名()”调用，该方法并非在vm实例或全局挂载在Vue.prototype上，无需this调用。
// import { saveData,getData } from '@/assets/localStorage.js';

export default {
  name: 'App',
  components: {
    TodoListHeader,
    TodoListMain,
    TodoListFooter
  },
 data() {
    return {
      contentList: [], // 初始化 contentList 属性为空数组
      throttleTimer: null,//节流标识符（自动本地存储节流1s）
      a:null,
      b:null,
    }
  },
  created() {
    // 使用挂载在Vue.prototype上的getData(),从本地存储中读取数据
   this.contentList = this.$getData('contentList');
  },
  watch: { // 利用watch选项，监控数据属性contentList，若有更新，即执行 本地存储！
    contentList: {
      handler() {
        if (! this.throttleTimer) {
          this.throttleTimer = setTimeout(() => {
            clearTimeout(this.throttleTimer);//清除计时器
            // 虽然清除了刚才计时器，但刚才计时器标识符的值（一个数字）还是原来的值，并没删除。必须置为null。以备下次符合判断条件，创建新的计时器。
             this.throttleTimer = null;//将变量值置为null，以备下次可进入判断
           this.$saveData('contentList', this.contentList);
        },1000)
        }
      }
    }
  },
  computed: {
    doneSum() {
      // 条目 总完成数
      let i=0;
      this.contentList.forEach(ele => {
        ele.status ? ++i : i;
      });
       return i;
    },
    checkSum() {
      // 1.所有条目的status，与门 后，关联total全选框，完善其功能
      // 全完成true 》false全部取消勾选；
      // 不全完成（都没完成、部分完成）false 》true全部勾选
      // 2.当this.contentList为 空数组 时，&&前true，&&后false。整体会返回false
     return this.contentList.every(ele => ele.status == true) && this.contentList.length;
    }
  },
  methods: {
    addTodo(obj) {
      this.contentList = [...this.contentList, obj];
      // this.contentList.push(obj);
    },
    check(id) {
      // 勾选某条目，改变其状态
      this.contentList.forEach(ele => {
        if (ele.id == id) {
          ele.status = !ele.status;
        }
      })
    },
    delTodo(id) {
      // 删除某条目
      let targetIndex = this.contentList.findIndex(ele => ele.id == id);
      this.contentList.splice(targetIndex,1);
    },
    checkAll(arg) {
      // 全选框，全选 》全不选；部分选或没选 》全选
      this.contentList = this.contentList.map(ele => { ele.status = arg; return ele; });
    },
    deleteAll() {
        //删除所有已完成，会得到 所有 未完成 条目 构成的 新数组
        this.contentList = this.contentList.filter(ele => ele.status != true);
    }
  },
  beforeDestroy() {
    // 确保组件销毁前，清除了延时器，防止内存泄漏
    clearTimeout(this.throttleTimer);
  }
}
</script>

<style lang="less">
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
